import meter1 from "../assets/img/meter1.svg";
import meter2 from "../assets/img/meter2.svg";
import meter3 from "../assets/img/meter3.svg";
import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';
import arrow1 from "../assets/img/arrow1.svg";
import arrow2 from "../assets/img/arrow2.svg";
import colorSharp from "../assets/img/color-sharp.png"

export const Skills = () => {
  const responsive = {
    superLargeDesktop: {
      // the naming can be any, depends on you.
      breakpoint: { max: 4000, min: 3000 },
      items: 5
    },
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 3
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1
    }
  };

  return (
    <section   id="skills"  className="skill" >
        <div className="container">
            <div className="row">
                <div className="col-12">
                    <div className="skill-bx wow zoomIn">
                        <h2>Skills</h2>
                        <p>Im curious, and I enjoy work that challenges me to learn something new and stretch in a different direction.<br></br> I do my best to stay on top of changes in the state of the art so that I can meet challenges with tools well suited to the job at hand.</p>
                        <Carousel responsive={responsive} infinite={true} className="owl-carousel owl-theme skill-slider">
                            <div class="skills-card" className="item">
                                <img  class="skills-card-img" src={meter1} alt="Image" />
                                <h5 class="skills-card-name">Web Development</h5>
                            </div>
                            <div class="skills-card" className="item">
                                <img  class="skills-card-img" src={meter2} alt="Image" />
                                <h5 class="skills-card-name">HTML</h5>
                            </div>
                            <div class="skills-card" className="item">
                                <img  class="skills-card-img" src={meter3} alt="Image" />
                                <h5 class="skills-card-name">CSS</h5>
                            </div>
                            <div class="skills-card" className="item">
                                <img  class="skills-card-img" src={meter1} alt="Image" />
                                <h5 class="skills-card-name">React</h5>
                            </div>
                            <div class="skills-card" className="item">
                                <img  class="skills-card-img" src={meter3} alt="Image" />
                                <h5 class="skills-card-name">Java</h5>
                            </div>
                            <div class="skills-card" className="item">
                                <img  class="skills-card-img" src={meter1} alt="Image" />
                                <h5 class="skills-card-name" >JavaScript</h5>
                            </div>
                        </Carousel>
                    </div>
                </div>
            </div>
        </div>
        <img className="background-image-left" src={colorSharp} alt="Image" />
    </section>
  )
}
